export const convertToLocalDate = (d: Date, language: string, options = {}) =>
  d.toLocaleDateString(language, options)

export const createGroupsInArray = (arr: any[], numberOfGroups: number) => {
  const perGroup = Math.ceil(arr.length / numberOfGroups)
  return new Array(numberOfGroups)
    .fill('')
    .map((_, i) => arr.slice(i * perGroup, (i + 1) * perGroup))
}

export const getCurrentYear = () => new Date().getFullYear() // year

export const getCurrentMonth = () => new Date().getMonth() // 0-11

export const getMonthName = (month: number, language: string) => {
  const d = new Date()
  d.setDate(1)
  d.setMonth(month)
  return d.toLocaleString(language, { month: 'long' })
}

export const getMonthsNames = (language: string) => {
  const months = []
  const d = new Date()
  d.setDate(1)

  for (let i = 0; i < 12; i++) {
    d.setMonth(i)
    months.push(d.toLocaleString(language, { month: 'short' }))
  }

  return months
}

const getLeadingDays = (year: number, month: number, firstDayOfWeek: number) => {
  // 0: sunday
  // 1: monday
  const dates = []
  const d = new Date(year, month)
  const y = d.getFullYear()
  const m = d.getMonth() - 1
  const firstWeekday = new Date(y, m, 1).getDay()
  let leadingDays = 6 - (6 - firstWeekday) - firstDayOfWeek

  if (firstDayOfWeek) {
    leadingDays = leadingDays < 0 ? 7 + leadingDays : leadingDays
  }

  for (let i = leadingDays * -1; i < 0; i++) {
    dates.push(new Date(y, m, i + 1))
  }

  return dates
}

const getMonthDays = (year: number, month: number) => {
  const dates = []
  const lastDay = new Date(year, month, 0).getDate()
  for (let i = 1; i <= lastDay; i++) {
    dates.push(new Date(year, month - 1, i))
  }
  return dates
}

const getTrailingDays = (year: number, month: number, leadingDays: Date[], monthDays: Date[]) => {
  const dates = []
  const days = 42 - (leadingDays.length + monthDays.length)
  for (let i = 1; i <= days; i++) {
    dates.push(new Date(year, month, i))
  }
  return dates
}

export const getMonthDetails = (year: number, month: number, firstDayOfWeek: number) => {
  const daysPrevMonth = getLeadingDays(year, month, firstDayOfWeek)
  const daysThisMonth = getMonthDays(year, month)
  const daysNextMonth = getTrailingDays(year, month, daysPrevMonth, daysThisMonth)
  const days = [...daysPrevMonth, ...daysThisMonth, ...daysNextMonth]

  const weeks: Date[][] = []

  days.forEach((day, index) => {
    if (index % 7 === 0 || weeks.length === 0) {
      weeks.push([])
    }
    weeks[weeks.length - 1].push(day)
  })

  return weeks
}

export const isInRange = (date: Date, start: Date, end: Date) => {
  return start < date && date < end
}

export const isSameDateAs = (date: Date, date2: Date) => {
  return (
    date.getDate() == date2.getDate() &&
    date.getMonth() == date2.getMonth() &&
    date.getFullYear() == date2.getFullYear()
  )
}

export const isToday = (date: Date) => {
  const today = new Date()
  return (
    date.getDate() == today.getDate() &&
    date.getMonth() == today.getMonth() &&
    date.getFullYear() == today.getFullYear()
  )
}
