// TODO: add disabled days

import React, { forwardRef, ReactNode, useEffect, useState } from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames'

import CIcon from '@coreui/icons-react'
import {
  cilChevronDoubleLeftAlt,
  cilChevronDoubleRightAlt,
  cilChevronLeftAlt,
  cilChevronRightAlt,
} from '@coreui/icons-pro'

import { CButton } from '@coreui/react-pro/src/index'
import {
  createGroupsInArray,
  getMonthDetails,
  getMonthsNames,
  isInRange,
  isSameDateAs,
  isToday,
} from './CDatePicker.utils'

export interface CDatePickerCalendarProps {
  /**
   * Default date of the component
   */
  calendarDate?: Date | string
  /**
   * A string of all className you want applied to the component.
   */
  className?: string
  /**
   * Initial selected to date (range).
   */
  endDate?: Date | string | null
  /**
   * Sets the day of start week.
   */
  firstDayOfWeek?:
    | 'monday'
    | 'tuesday'
    | 'wednesday'
    | 'thursday'
    | 'friday'
    | 'saturday'
    | 'sunday'
  /**
   * Sets the default locale for components.
   */
  locale?: string
  /**
   * Show navigation.
   */
  navigation?: boolean
  /**
   * The custom next icon.
   */
  nextIcon?: ReactNode
  /**
   * The custom next double icon.
   */
  nextDoubleIcon?: ReactNode
  /**
   * The custom prev icon.
   */
  prevIcon?: ReactNode
  /**
   * The custom prev double icon.
   */
  prevDoubleIcon?: ReactNode
  /**
   * Allow range selection.
   */
  range?: boolean
  /**
   * Enables Right-to-left text direction for this component.
   */
  rtl?: boolean
  /**
   * The custom next icon (Right-to-left).
   */
  rtlNextIcon?: ReactNode
  /**
   * The custom next double icon (Right-to-left).
   */
  rtlNextDoubleIcon?: ReactNode
  /**
   * The custom prev icon (Right-to-left).
   */
  rtlPrevIcon?: ReactNode
  /**
   * The custom prev double icon (Right-to-left).
   */
  rtlPrevDoubleIcon?: ReactNode
  /**
   * Toggle select mode between start and end date.
   */
  selectEnd?: boolean
  /**
   * Initial selected date.
   */
  startDate?: Date | string | null
  /**
   * Callback fired when the user hovers over the calendar cell.
   */
  onCalendarCellHover?: (date: Date | null) => void
  /**
   * Callback fired when the calendar date changed.
   */
  onCalendarDateChange?: (date: Date) => void
  /**
   * Callback fired when the start date changed.
   */
  onStartDateChange?: (date: Date) => void
  /**
   * Callback fired when the start date changed.
   */
  onEndDateChange?: (date: Date) => void
}

const WEEK_DAYS = {
  sunday: 0,
  monday: 1,
  tuesday: 2,
  wednesday: 3,
  thursday: 4,
  friday: 5,
  saturday: 6,
}

export const CDatePickerCalendar = forwardRef<HTMLDivElement, CDatePickerCalendarProps>(
  (
    {
      calendarDate,
      endDate,
      firstDayOfWeek = 'monday',
      locale = 'en-US',
      navigation = true,
      nextIcon = <CIcon icon={cilChevronRightAlt} />,
      nextDoubleIcon = <CIcon icon={cilChevronDoubleRightAlt} />,
      prevIcon = <CIcon icon={cilChevronLeftAlt} />,
      prevDoubleIcon = <CIcon icon={cilChevronDoubleLeftAlt} />,
      range,
      rtl,
      rtlNextIcon = <CIcon icon={cilChevronLeftAlt} />,
      rtlNextDoubleIcon = <CIcon icon={cilChevronDoubleLeftAlt} />,
      rtlPrevIcon = <CIcon icon={cilChevronRightAlt} />,
      rtlPrevDoubleIcon = <CIcon icon={cilChevronDoubleRightAlt} />,
      selectEnd,
      startDate,
      onCalendarDateChange,
      onStartDateChange,
      onEndDateChange,
      onCalendarCellHover,
    },
    ref,
  ) => {
    const [_startDate, setStartDate] = useState<Date | null>(startDate ? new Date(startDate) : null)
    const [_endDate, setEndDate] = useState<Date | null>(endDate ? new Date(endDate) : null)

    const [_selectEnd, setSelectEnd] = useState(selectEnd)

    const [_calendarDate, setCalendarDate] = useState<Date>(
      calendarDate ? new Date(calendarDate) : new Date(),
    )

    // view type
    const [view, setView] = useState('days')

    useEffect(() => {
      if (calendarDate) {
        const d = new Date(calendarDate)
        !isSameDateAs(_calendarDate, d) && setCalendarDate(d)
      }
    }, [calendarDate])

    useEffect(() => {
      if (startDate === null) {
        setStartDate(null)
        return
      }

      if (startDate) {
        const d = new Date(startDate)

        if (_startDate === null) {
          setStartDate(d)
          return
        }

        if (_startDate && !isSameDateAs(d, _startDate)) {
          setStartDate(d)
        }
      }
    }, [startDate])

    useEffect(() => {
      if (endDate === null) {
        setEndDate(null)
        return
      }

      if (endDate) {
        const d = new Date(endDate)

        if (_endDate === null) {
          setEndDate(d)
          return
        }

        if (_endDate && !isSameDateAs(d, _endDate)) {
          setEndDate(d)
        }
      }
    }, [endDate])

    useEffect(() => {
      setSelectEnd(selectEnd)
    }, [selectEnd])

    useEffect(() => {
      _startDate && onStartDateChange && onStartDateChange(_startDate)
    }, [_startDate])

    useEffect(() => {
      _endDate && onEndDateChange && onEndDateChange(_endDate)
    }, [_endDate])

    // Get localized data
    const _locale = locale === 'auto' ? navigator.language : locale

    const setCalendarPage = (years: number, months = 0) => {
      const year = _calendarDate.getFullYear()
      const month = _calendarDate.getMonth()
      const d = new Date(year, month, 1)

      years && d.setFullYear(d.getFullYear() + years)
      months && d.setMonth(d.getMonth() + months)

      setCalendarDate(d)
      onCalendarDateChange && onCalendarDateChange(d)
    }

    const handleCellOnClick = (date: Date) => {
      if (range) {
        if (_selectEnd) {
          // If endDate > date
          if (_startDate && _startDate > date) {
            setEndDate(_startDate)
            setStartDate(date)
          } else {
            setEndDate(date)
          }
          setSelectEnd(false)
        } else {
          setStartDate(date)
          setSelectEnd(true)
        }
      } else {
        setStartDate(date)
      }
    }

    const handleCellMouseEnter = (date: Date) => {
      onCalendarCellHover && onCalendarCellHover(date)
    }

    const handleCellMouseLeave = () => {
      onCalendarCellHover && onCalendarCellHover(null)
    }

    const handleNavigationOnClick = (direction: string, double = false) => {
      if (direction === 'prev') {
        if (double) {
          setCalendarPage(view !== 'days' ? -10 : -1)
          return
        }
        if (view !== 'days') {
          setCalendarPage(-1)
          return
        }
        setCalendarPage(0, -1)
        return
      }
      if (direction === 'next') {
        if (double) {
          setCalendarPage(view !== 'days' ? 10 : 1)
          return
        }
        if (view !== 'days') {
          setCalendarPage(1)
          return
        }
        setCalendarPage(0, 1)
        return
      }
    }

    const Calendar = () => {
      const _year = _calendarDate.getFullYear()
      const _month = _calendarDate.getMonth()

      const monthDetails = getMonthDetails(_year, _month + 1, WEEK_DAYS[firstDayOfWeek])
      const listOfMonths = createGroupsInArray(getMonthsNames(_locale), 4)

      const years = []
      for (let year = _year - 6; year < _year + 6; year++) {
        years.push(year)
      }
      const listOfYears = createGroupsInArray(years, 4)
      const weekDays = monthDetails[0]
      return (
        <table>
          {view === 'days' && (
            <thead>
              <tr>
                {weekDays.map((day: any, idx: number) => (
                  <th key={idx} className="datepicker-cell">
                    {day.toLocaleDateString(_locale, { weekday: 'narrow' })}
                  </th>
                ))}
              </tr>
            </thead>
          )}
          <tbody>
            {view === 'days' &&
              monthDetails.map((week, index) => {
                return (
                  <tr key={index}>
                    {week.map((day: Date, idx: number) => (
                      <td
                        className="datepicker-cell"
                        key={idx}
                        title={day.toLocaleDateString(_locale)}
                        onClick={() => handleCellOnClick(day)}
                        onMouseEnter={() => handleCellMouseEnter(day)}
                        onMouseLeave={() => handleCellMouseLeave()}
                      >
                        <div
                          className={classNames('datepicker-cell-inner', {
                            current: isToday(day),
                            next: day.getDate() < (index - 1) * 7 + idx,
                            previous: day.getDate() > index * 7 + idx + 1,
                            range: _startDate && _endDate && isInRange(day, _startDate, _endDate),
                            selected:
                              (_startDate && isSameDateAs(_startDate, day)) ||
                              (_endDate && isSameDateAs(_endDate, day)),
                            start:
                              _startDate &&
                              _endDate &&
                              isSameDateAs(_startDate, day) &&
                              _startDate < _endDate,
                            end:
                              _startDate &&
                              _endDate &&
                              isSameDateAs(_endDate, day) &&
                              _startDate < _endDate,
                          })}
                        >
                          {day.toLocaleDateString(_locale, { day: 'numeric' })}
                        </div>
                      </td>
                    ))}
                  </tr>
                )
              })}
            {view === 'months' &&
              listOfMonths.map((row, index) => {
                return (
                  <tr key={index}>
                    {row.map((month, idx) => {
                      return (
                        <td
                          key={idx}
                          className="datepicker-cell month"
                          onClick={() => {
                            setCalendarPage(0, index * 3 + idx)
                            setView('days')
                          }}
                        >
                          <div className="datepicker-cell-inner">{month}</div>
                        </td>
                      )
                    })}
                  </tr>
                )
              })}
            {view === 'years' &&
              listOfYears.map((row, index) => {
                return (
                  <tr key={index}>
                    {row.map((year, idx) => {
                      return (
                        <td
                          key={idx}
                          className="datepicker-cell month"
                          onClick={() => {
                            setCalendarDate(
                              new Date(year, _calendarDate.getMonth(), _calendarDate.getDate()),
                            )
                            setView('months')
                          }}
                        >
                          <div className="datepicker-cell-inner">{year}</div>
                        </td>
                      )
                    })}
                  </tr>
                )
              })}
          </tbody>
        </table>
      )
    }

    const Navigation = () => {
      return (
        <div className="datepicker-navigation">
          {navigation && (
            <div className="datepicker-navigation-prev">
              <CButton
                color="transparent"
                size="sm"
                onClick={() => handleNavigationOnClick('prev', true)}
              >
                {rtl ? rtlPrevDoubleIcon : prevDoubleIcon}
              </CButton>
              <CButton
                color="transparent"
                size="sm"
                onClick={() => handleNavigationOnClick('prev')}
              >
                {rtl ? rtlPrevIcon : prevIcon}
              </CButton>
            </div>
          )}
          <div className="datepicker-navigation-date">
            {view === 'days' && (
              <CButton
                color="transparent"
                size="sm"
                onClick={() => navigation && setView('months')}
              >
                {_calendarDate.toLocaleDateString(_locale, { month: 'long' })}
              </CButton>
            )}
            <CButton color="transparent" size="sm" onClick={() => navigation && setView('years')}>
              {_calendarDate.toLocaleDateString(_locale, { year: 'numeric' })}
            </CButton>
          </div>
          {navigation && (
            <div className="datepicker-navigation-next">
              <CButton
                color="transparent"
                size="sm"
                onClick={() => handleNavigationOnClick('next')}
              >
                {rtl ? rtlNextIcon : nextIcon}
              </CButton>
              <CButton
                color="transparent"
                size="sm"
                onClick={() => handleNavigationOnClick('next', true)}
              >
                {rtl ? rtlNextDoubleIcon : nextDoubleIcon}
              </CButton>
            </div>
          )}
        </div>
      )
    }

    return (
      <div className="datepicker-calendar" ref={ref}>
        <Navigation />
        <Calendar />
      </div>
    )
  },
)

CDatePickerCalendar.displayName = 'CDatePickerCalendar'

CDatePickerCalendar.propTypes = {
  className: PropTypes.string,
  calendarDate: PropTypes.oneOfType([PropTypes.instanceOf(Date), PropTypes.string]),
  endDate: PropTypes.oneOfType([PropTypes.instanceOf(Date), PropTypes.string]),
  firstDayOfWeek: PropTypes.oneOf([
    'monday',
    'tuesday',
    'wednesday',
    'thursday',
    'friday',
    'saturday',
    'sunday',
  ]),
  locale: PropTypes.string,
  navigation: PropTypes.bool,
  nextIcon: PropTypes.node,
  nextDoubleIcon: PropTypes.node,
  prevIcon: PropTypes.node,
  prevDoubleIcon: PropTypes.node,
  range: PropTypes.bool,
  rtl: PropTypes.bool,
  rtlNextIcon: PropTypes.node,
  rtlNextDoubleIcon: PropTypes.node,
  rtlPrevIcon: PropTypes.node,
  rtlPrevDoubleIcon: PropTypes.node,
  selectEnd: PropTypes.bool,
  startDate: PropTypes.oneOfType([PropTypes.instanceOf(Date), PropTypes.string]),
  onCalendarCellHover: PropTypes.func,
  onCalendarDateChange: PropTypes.func,
  onStartDateChange: PropTypes.func,
  onEndDateChange: PropTypes.func,
}
