import React, { forwardRef, ReactNode, useEffect, useRef, useState } from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames'
import CIcon from '@coreui/icons-react'
import { cilArrowLeft, cilArrowRight, cilCalendar, cilCalendarTimes } from '@coreui/icons-pro'
import { CDatePickerCalendar, CDatePickerCalendarProps } from '../datepicker/CDatePickerCalendar'
import { convertToLocalDate, isSameDateAs } from '../datepicker/CDatePicker.utils'
import {
  CDropdown,
  CDropdownMenu,
  CDropdownToggleCustom,
  CFormInput,
  CInputGroup,
  CInputGroupText,
} from '@coreui/react-pro/src/index'

// Import styles
import './CDateRangePicker.scss'

export interface CDateRangePickerProps
  extends Omit<CDatePickerCalendarProps, 'onCalendarCellHover' | 'onCalendarDateChange'> {
  /**
   * Default icon between date input fields.
   */
  arrowIcon?: ReactNode | boolean
  /**
   * Enables selection cleaner element.
   */
  cleaner?: boolean
  /**
   * Cleaner icon.
   */
  cleanerIcon?: ReactNode | boolean
  /**
   * Toggle the disabled state for the component.
   */
  disabled?: boolean
  /**
   * Default input icon.
   */
  inputIcon?: ReactNode | boolean
  /**
   * Toggle the readonly state for the component.
   */
  inputReadOnly?: boolean
  /**
   * Specifies short hints that are visible in start date and end date inputs.
   */
  placeholders?: string[]
  /**
   * Default icon between date input fields  (Right-to-left).
   */
  rtlArrowIcon?: ReactNode | boolean
  /**
   * Toggle the visibility of component.
   */
  visible?: boolean
}

export const CDateRangePicker = forwardRef<HTMLDivElement, CDateRangePickerProps>(
  (
    {
      arrowIcon = <CIcon icon={cilArrowRight} />,
      className,
      cleaner = true,
      cleanerIcon = <CIcon icon={cilCalendarTimes} />,
      calendarDate,
      disabled,
      endDate,
      inputIcon = <CIcon icon={cilCalendar} />,
      inputReadOnly,
      locale = 'en-US',
      onEndDateChange,
      onStartDateChange,
      placeholders = ['Start date', 'End date'],
      range = true,
      rtl,
      rtlArrowIcon = <CIcon icon={cilArrowLeft} />,
      startDate,
      visible,
      ...rest
    },
    ref,
  ) => {
    const [_visible, setVisible] = useState(visible)

    const inputStartRef = useRef<HTMLInputElement>(null)
    const inputEndRef = useRef<HTMLInputElement>(null)
    const [selectedDate, setSelectedDate] = useState<Date | null>(
      startDate ? new Date(startDate) : null,
    )
    const [selectedDateTo, setSelectedDateTo] = useState<Date | null>(
      endDate ? new Date(endDate) : null,
    )

    const [inputHoverValue, setInputHoverValue] = useState<Date | null>(null)
    const [inputToHoverValue, setInputToHoverValue] = useState<Date | null>(null)

    const [selectTo, setSelectTo] = useState(false)

    const [_calendarDate, setCalendarDate] = useState<Date>(
      calendarDate ? new Date(calendarDate) : new Date(),
    )

    useEffect(() => {
      startDate && setCalendarDate(new Date(startDate))
    }, [startDate])

    useEffect(() => {
      endDate && setCalendarDate(new Date(endDate))
    }, [endDate])

    useEffect(() => {
      if (inputStartRef.current) {
        if (inputHoverValue) {
          inputStartRef.current.value = convertToLocalDate(inputHoverValue, _locale)
          return
        }
        if (selectedDate) {
          inputStartRef.current.value = convertToLocalDate(selectedDate, _locale)
          return
        }
        inputStartRef.current.value = ''
      }
    }, [inputHoverValue, selectedDate])

    useEffect(() => {
      if (inputEndRef.current) {
        if (inputToHoverValue) {
          inputEndRef.current.value = convertToLocalDate(inputToHoverValue, _locale)
          return
        }
        if (selectedDateTo) {
          inputEndRef.current.value = convertToLocalDate(selectedDateTo, _locale)
          return
        }
        inputEndRef.current.value = ''
      }
    }, [inputToHoverValue, selectedDateTo])

    // Get localized data
    const _locale = locale === 'auto' ? navigator.language : locale

    const handleCalendarDateChange = (date: Date) => {
      setCalendarDate(date)
    }

    const handleCalendarDateNextChange = (date: Date) => {
      setCalendarDate(new Date(date.getFullYear(), date.getMonth() - 1, 1))
    }

    const handleStartDateChange = (date: Date) => {
      if (selectedDate === null || (selectedDate && !isSameDateAs(date, selectedDate))) {
        setSelectedDate(date)
        setSelectTo(true)
        onStartDateChange && onStartDateChange(date)
      }
      setInputHoverValue(null)
    }

    const handleEndDateChange = (date: Date) => {
      if (selectedDateTo === null || (selectedDateTo && !isSameDateAs(date, selectedDateTo))) {
        setSelectedDateTo(date)
        setSelectTo(false)
        onEndDateChange && onEndDateChange(date)

        selectedDate && setVisible(false)
      }
      setInputToHoverValue(null)
    }

    const handleCalendarCellHover = (date: Date | null) => {
      selectTo ? setInputToHoverValue(date) : setInputHoverValue(date)
    }

    const handleClear = (event: React.MouseEvent<HTMLElement>) => {
      event.stopPropagation()
      setSelectedDate(null)
      setSelectedDateTo(null)
      setInputHoverValue(null)
      setInputToHoverValue(null)
    }

    const _className = classNames('datepicker', { rtl: rtl }, className)

    return (
      <CDropdown
        className={_className}
        variant="dropdown"
        visible={_visible}
        onShow={() => setVisible(true)}
        {...rest}
        ref={ref}
      >
        <CDropdownToggleCustom disabled={disabled} trigger={'click-init'}>
          <CInputGroup>
            <CFormInput
              className={classNames({
                hover: inputHoverValue,
              })}
              disabled={disabled}
              placeholder={placeholders[0]}
              readOnly={inputReadOnly}
              onClick={() => setSelectTo(false)}
              ref={inputStartRef}
            />
            {arrowIcon && <CInputGroupText>{rtl ? rtlArrowIcon : arrowIcon}</CInputGroupText>}
            <CFormInput
              className={classNames({
                hover: inputToHoverValue,
              })}
              disabled={disabled}
              placeholder={placeholders[1]}
              readOnly={inputReadOnly}
              onClick={() => setSelectTo(true)}
              ref={inputEndRef}
            />
            {(inputIcon || cleaner) && (
              <CInputGroupText>
                {inputIcon && <span className="datepicker-indicator">{inputIcon}</span>}
                {cleaner && selectedDate && (
                  <span
                    className="datepicker-cleaner"
                    role="button"
                    onClick={(event) => handleClear(event)}
                  >
                    {cleanerIcon}
                  </span>
                )}
              </CInputGroupText>
            )}
          </CInputGroup>
        </CDropdownToggleCustom>
        <CDropdownMenu component="div">
          <div className="datepicker-calendars">
            <CDatePickerCalendar
              calendarDate={new Date(_calendarDate.getFullYear(), _calendarDate.getMonth(), 1)}
              endDate={selectedDateTo}
              range={range}
              rtl={rtl}
              selectEnd={selectTo}
              startDate={selectedDate}
              onCalendarCellHover={(date) => handleCalendarCellHover(date)}
              onCalendarDateChange={(date) => handleCalendarDateChange(date)}
              onStartDateChange={(date) => handleStartDateChange(date)}
              onEndDateChange={(date) => handleEndDateChange(date)}
              {...rest}
            />
            <CDatePickerCalendar
              calendarDate={new Date(_calendarDate.getFullYear(), _calendarDate.getMonth() + 1, 1)}
              endDate={selectedDateTo}
              range={range}
              rtl={rtl}
              selectEnd={selectTo}
              startDate={selectedDate}
              onCalendarCellHover={(date) => handleCalendarCellHover(date)}
              onCalendarDateChange={(date) => handleCalendarDateNextChange(date)}
              onStartDateChange={(date) => handleStartDateChange(date)}
              onEndDateChange={(date) => handleEndDateChange(date)}
              {...rest}
            />
          </div>
        </CDropdownMenu>
      </CDropdown>
    )
  },
)

CDateRangePicker.displayName = 'CDateRangePicker'

CDateRangePicker.propTypes = {
  ...CDatePickerCalendar.propTypes,
  disabled: PropTypes.bool,
  inputReadOnly: PropTypes.bool,
  placeholders: PropTypes.arrayOf(PropTypes.any),
}
