import React, { forwardRef, ReactNode, useEffect, useRef, useState } from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames'
import CIcon from '@coreui/icons-react'
import { cilCalendar, cilCalendarTimes } from '@coreui/icons-pro'
import { CDatePickerCalendar, CDatePickerCalendarProps } from './CDatePickerCalendar'
import { convertToLocalDate, isSameDateAs } from './CDatePicker.utils'
import {
  CDropdown,
  CDropdownMenu,
  CDropdownToggleCustom,
  CFormInput,
  CInputGroup,
  CInputGroupText,
} from '@coreui/react-pro/src/index'

// Import styles
import './CDatePicker.scss'

export interface CDatePickerProps
  extends Omit<
    CDatePickerCalendarProps,
    | 'endDate'
    | 'onCalendarCellHover'
    | 'onCalendarDateChange'
    | 'onEndDateChange'
    | 'range'
    | 'startDate'
    | 'selectEnd'
  > {
  /**
   * Enables selection cleaner element.
   */
  cleaner?: boolean
  /**
   * Cleaner icon.
   */
  cleanerIcon?: ReactNode | boolean
  /**
   * Initial selected date.
   */
  date?: Date | string | null
  /**
   * Toggle the disabled state for the component.
   */
  disabled?: boolean
  /**
   * Default input icon.
   */
  inputIcon?: ReactNode | boolean
  /**
   * Toggle the readonly state for the component.
   */
  inputReadOnly?: boolean
  /**
   * Specifies a short hint that is visible in the input.
   */
  placeholder?: string
  /**
   * Toggle the visibility of component.
   */
  visible?: boolean
  /**
   * Callback fired when the date changed.
   */
  onDateChange?: (date: Date) => void
}

export const CDatePicker = forwardRef<HTMLDivElement, CDatePickerProps>(
  (
    {
      calendarDate,
      className,
      cleaner = true,
      cleanerIcon = <CIcon icon={cilCalendarTimes} />,
      date,
      disabled,
      inputIcon = <CIcon icon={cilCalendar} />,
      inputReadOnly,
      locale = 'en-US',
      onDateChange,
      placeholder = 'Select date',
      rtl,
      visible,
      ...rest
    },
    ref,
  ) => {
    const [_visible, setVisible] = useState(visible)

    const inputRef = useRef<HTMLInputElement>(null)

    const [selectedDate, setSelectedDate] = useState<Date | null>(date ? new Date(date) : null)

    const [inputHoverValue, setInputHoverValue] = useState<Date | null>(null)

    const [_calendarDate, setCalendarDate] = useState<Date>(
      calendarDate ? new Date(calendarDate) : new Date(),
    )

    useEffect(() => {
      date && setCalendarDate(new Date(date))
    }, [date])

    useEffect(() => {
      if (inputRef.current) {
        if (inputHoverValue) {
          inputRef.current.value = convertToLocalDate(inputHoverValue, _locale)
          return
        }
        if (selectedDate) {
          inputRef.current.value = convertToLocalDate(selectedDate, _locale)
          return
        }
        inputRef.current.value = ''
      }
    }, [inputHoverValue, selectedDate])

    // Get localized data
    const _locale = locale === 'auto' ? navigator.language : locale

    // Handlers
    const handleCalendarDateChange = (date: Date) => {
      setCalendarDate(date)
      onDateChange && onDateChange(date)
    }

    const handleStartDateChange = (date: Date) => {
      if (selectedDate === null || (selectedDate && !isSameDateAs(date, selectedDate))) {
        setSelectedDate(date)
        setVisible(false)
      }
      setInputHoverValue(null)
    }

    const handleCalendarCellHover = (date: Date | null) => setInputHoverValue(date)

    const handleClear = (event: React.MouseEvent<HTMLElement>) => {
      event.stopPropagation()
      setSelectedDate(null)
      setInputHoverValue(null)
    }

    const _className = classNames('datepicker', { rtl: rtl }, className)

    return (
      <CDropdown
        className={_className}
        variant="dropdown"
        visible={_visible}
        onShow={() => setVisible(true)}
        {...rest}
        ref={ref}
      >
        <CDropdownToggleCustom disabled={disabled} trigger={'click-init'}>
          <CInputGroup>
            <CFormInput
              className={classNames({
                hover: inputHoverValue,
              })}
              disabled={disabled}
              placeholder={placeholder}
              readOnly={inputReadOnly}
              ref={inputRef}
            />
            {(inputIcon || cleaner) && (
              <CInputGroupText>
                {inputIcon && <span className="datepicker-indicator">{inputIcon}</span>}
                {cleaner && selectedDate && (
                  <span
                    className="datepicker-cleaner"
                    role="button"
                    onClick={(event) => handleClear(event)}
                  >
                    {cleanerIcon}
                  </span>
                )}
              </CInputGroupText>
            )}
          </CInputGroup>
        </CDropdownToggleCustom>
        <CDropdownMenu component="div">
          <div className="datepicker-calendars">
            <CDatePickerCalendar
              calendarDate={new Date(_calendarDate.getFullYear(), _calendarDate.getMonth(), 1)}
              locale={_locale}
              rtl={rtl}
              startDate={selectedDate}
              onCalendarCellHover={(date) => handleCalendarCellHover(date)}
              onCalendarDateChange={(date) => handleCalendarDateChange(date)}
              onStartDateChange={(date) => handleStartDateChange(date)}
              {...rest}
            />
          </div>
        </CDropdownMenu>
      </CDropdown>
    )
  },
)

CDatePicker.displayName = 'CDatePicker'

CDatePicker.propTypes = {
  ...CDatePickerCalendar.propTypes,
  date: PropTypes.oneOfType([PropTypes.instanceOf(Date), PropTypes.string]),
  disabled: PropTypes.bool,
  inputReadOnly: PropTypes.bool,
  onDateChange: PropTypes.func,
  placeholder: PropTypes.string,
}
